@media screen and (min-width: 380px) {
  .wrap__write-us-contant {
   padding-top: 50px;
    align-items: center;
    justify-content: center;
    position: relative;
    display: flex;
  flex-direction: column;
}
  .write-us__contant {
  padding: 30px 30px;
  position: relative;
 width: 100%;
 max-width: 400px;
}
.forma__container {
  padding: 20px 0px;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 30px;
  width: 100%;

}
.write-us__title {
  font-size: 48px;
  color: #515151;
  font-weight: 700;
  text-align: center;
}
.write-us__forma {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
 }
.write-us__wrap-input {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  font-size: 16px;
}
.write-us__wrap-input:focus::placeholder,
.write-us__wrap-input input:hover::placeholder{
  display: none;
}
.write-us__input {
  font-weight: 400;
  font-family: 'JournalSans';
  border: none;
  border-bottom: 1px solid #000;
  color: #000;
  font-size: 16px;
  outline: none;
}

.select__region {
  font-size: 16px;
  border: none;
  border-bottom: 1px solid #000;
  color: #000;
  letter-spacing: 1.2;
}
.forma__radio {
  display: flex;
  justify-content: space-between;
  align-items: center;
 /* gap: 120px;*/
  padding: 10px 0px;
  width: 100%;
}

.input__radio {
  font-size: 16px;
  width: 20px;
  height: 20px;
}
.label__radio {
  font-size: 25px;
  color: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}
.forma__button {
  text-transform: uppercase;
  font-size: 20px;
  margin-top: 10px;
  display: inline-block;
  background-color: transparent;
  padding: 25px 110px;
  letter-spacing: 2;
  width: 100%;
}
.pagination__container.writeus-pagination__container {
  padding: 40px 0px 0px 0px;
}
