  transition: opacity 0.5s ease;
}*/
}
@media screen and (min-width: 768px) {
  .write-us__contant {
   width: 100%;
    max-width:700px;
   
}
}
@media screen and (min-width: 992px) {
  .write-us__contant {
    width:100%;
    max-width: 800px;
}
.write-us__contant {
  padding: 30px 30px;
  position: relative;
 width: 100%;
 max-width: 800px;
}
.wrap__write-us-contant {
  padding-top: 50px;
 align-items: start;
}

.write-us__forma {
  align-items: stretch;
}
.forma__container {
  width: 100%;
  max-width: 800px;
}
.write-us__title {
 text-align: left;
}
}