body {
  max-width: 1920px;
  width: 100%;
  background-color: #fff;
  justify-content: center;
  font-size: 16px;
}

@media screen and (min-width: 380px) {
  .slide, .wrap__slide {
    width: 100%;
    height: 100%;
    display: block;
    position: relative;
  }

  .wrap-container__mob-slide, .container__mob-slide {
    width: 100%;
    height: 100%;
    z-index: 31;
    position: relative;
  }

  .wrap-container__left-slide, .wrap-container__right-slide {
    width: 100%;
    height: 100%;
    display: block;
  }

  .wrap-slide__container {
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    display: flex;
    position: relative;
    overflow: hidden;
  }

  .container__right-slide, .container__left-slide {
    width: 300%;
    height: 100%;
  }

  .wrap__img {
    display: none;
  }

  .img__slide {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }

  .wrap__mob-slide {
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
    position: relative;
    overflow: hidden;
  }

  .wrap__slide-up, .wrap__slide-down {
    width: 100%;
    height: 100%;
    display: block;
    position: relative;
  }

  .mob__slide {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }

  .wrap__left-slide, .wrap__right-slide {
    width: 100%;
    height: 100%;
    flex-direction: row;
    transition: transform 16s;
    animation: animationRight 16s linear infinite alternate forwards;
    display: flex;
    position: relative;
    transform: translateX(0%);
  }

  @keyframes animationRight {
    0%, 100% {
      transform: translateX(-66.7%);
    }

    10%, 90% {
      transform: translateX(-66.7%);
    }

    20%, 80% {
      transform: translateX(-33%);
    }

    30%, 70% {
      transform: translateX(-33%);
    }

    40%, 60% {
      transform: translateX(0%);
    }

    50% {
      transform: translateX(0%);
    }
  }

  @keyframes animationLeft {
    0%, 100% {
      transform: translateX(0%);
    }

    10%, 90% {
      transform: translateX(0%);
    }

    20%, 80% {
      transform: translateX(-35%);
    }

    30%, 70% {
      transform: translateX(-35%);
    }

    40%, 60% {
      transform: translateX(-66.7%);
    }

    50% {
      transform: translateX(-66.7%);
    }
  }

  .mob__img-slide {
    width: 100%;
    height: 100%;
    display: block;
    position: relative;
  }

  .img-slide__mob {
    width: 100%;
    height: 100%;
    display: block;
  }

  .swiper__hero {
    z-index: 60;
    height: 820px;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    overflow: hidden;
  }

  .tablet__img {
    display: none;
  }
}

@media (min-width: 768px) {
  .swiper-slide__left {
    width: 100%;
    height: 100%;
    flex: 1;
    justify-content: center;
    align-items: center;
    display: block;
  }

  .swiper-slide__left, .swiper-slide__right {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }

  .tablet__img, .img__slide {
    width: 100%;
    height: 100%;
    display: block;
  }

  .mob__slide {
    display: none;
  }

  .container__right-slide, .container__left-slide {
    width: 300%;
    height: 100%;
    display: block;
  }

  @keyframes animationRight {
    0%, 100% {
      transform: translateX(-66.7%);
    }

    10%, 90% {
      transform: translateX(-66.7%);
    }

    20%, 80% {
      transform: translateX(-33%);
    }

    30%, 70% {
      transform: translateX(-33%);
    }

    40%, 60% {
      transform: translateX(0%);
    }

    50% {
      transform: translateX(0%);
    }
  }

  .wrap__left-slide {
    flex-direction: row;
    transition: transform 16s;
    animation: animationLeft 16s linear infinite alternate forwards;
    transform: translateX(0%);
  }

  @keyframes animationLeft {
    0%, 100% {
      transform: translateX(0%);
    }

    10%, 90% {
      transform: translateX(0%);
    }

    20%, 80% {
      transform: translateX(-35%);
    }

    30%, 70% {
      transform: translateX(-35%);
    }

    40%, 60% {
      transform: translateX(-66.7%);
    }

    50% {
      transform: translateX(-66.7%);
    }
  }

  .swiper-slide__left :nth-child(1) {
    animation-delay: 1s;
  }

  .swiper-slide__left :nth-child(2) {
    animation-delay: 2s;
  }

  .swiper-slide__left:nth-child(3) {
    animation-delay: 3s;
  }
}

@media screen and (min-width: 1200px) {
  .wrap-container__mob-slide {
    display: none;
  }

  .wrap__img {
    display: block;
  }

  .img-slide__mob, .mob__slide, .tablet__img {
    display: none;
  }

  .wrap-slide__container {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    display: flex;
    position: relative;
    overflow: hidden;
  }

  .container__right-slide, .container__left-slide {
    width: 100%;
    height: 100%;
  }

  .swiper-slide__left, .swiper-slide__right {
    flex: 1;
  }

  .img-slide__desc {
    width: 100%;
    height: 100%;
    display: block;
  }

  .img-slide__mob {
    display: none;
  }

  .wrap__img {
    display: block;
  }

  .wrap__slide {
    width: 100%;
    height: 100%;
    z-index: 31;
    position: absolute;
  }

  .wrap-slide__container.element.style {
    display: block;
  }

  .wrap__left-slide {
    flex-direction: column;
    transition: transform 20s;
    animation: animationLeft 16s linear infinite alternate forwards;
    transform: translateY(0%);
  }

  @keyframes animationLeft {
    0%, 100% {
      transform: translateY(0%);
    }

    10%, 90% {
      transform: translateY(0%);
    }

    20%, 80% {
      transform: translateY(-100%);
    }

    30%, 70% {
      transform: translateY(-100%);
    }

    40%, 60% {
      transform: translateY(-200%);
    }

    50% {
      transform: translateY(-200%);
    }
  }

  .swiper-slide__left :nth-child(1) {
    animation-delay: 1s;
  }

  .swiper-slide__left :nth-child(2) {
    animation-delay: 2s;
  }

  .swiper-slide__left:nth-child(3) {
    animation-delay: 3s;
  }

  .wrap__right-slide {
    flex-direction: column;
    margin: 0;
    transition: transform 20s;
    animation: animationRight 16s linear infinite alternate forwards;
    display: flex;
    position: relative;
  }

  @keyframes animationRight {
    0%, 100% {
      transform: translateY(-200%);
    }

    10%, 90% {
      transform: translateY(-200%);
    }

    20%, 80% {
      transform: translateY(-100%);
    }

    30%, 70% {
      transform: translateY(-100%);
    }

    40%, 60% {
      transform: translateY(0%);
    }

    50% {
      transform: translateY(0%);
    }
  }

  .wrap__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: relative;
  }

  .button__menu-open:focus animationLeft, animationLeft {
    animation-play-state: paused;
  }

  .title__right {
    max-width: 1920px;
    width: 100%;
    height: 100%;
    color: #fff;
    text-transform: uppercase;
    background-color: rgba(47, 48, 58, .4);
    padding-top: 300px;
    padding-left: 0%;
    font-family: Fiver;
    font-size: 100px;
    font-weight: 400;
    line-height: .5;
    position: absolute;
    top: 0%;
  }

  .title__left {
    width: 100%;
    height: 100%;
    color: #fff;
    text-transform: uppercase;
    text-align: right;
    background-color: rgba(47, 48, 58, .4);
    padding-top: 300px;
    font-family: Fiver;
    font-size: 100px;
    font-weight: 400;
    line-height: .5;
    position: absolute;
    top: 0%;
  }

  .img__left-slide {
    position: relative;
  }

  .wrap__img {
    width: 100%;
    height: 100%;
    align-items: center;
    display: flex;
    position: relative;
  }

  .img-slide__desc {
    position: relative;
  }

  .logo {
    z-index: 3;
    position: absolute;
    top: 20px;
    left: 20px;
  }
}

@media screen and (min-width: 1400px) {
  .title__right, .title__left {
    font-size: 130px;
  }
}

