@media screen and (min-width: 380px) {
.about-us-content__link,
.contacts__link-content {
  justify-content: center;
  flex-direction: column;
align-items: flex-end;
  gap: 5px;
  display: flex;
  position: relative; 
}
.page__contacts {
  display: block;
width: 100%;
height: 100%;
background-color: #000;

}
  .page-hero__link {
    color: #fff;
    text-transform: uppercase;
    width: 150px;
    text-align: right;
    background-color: #0000;
  font-size: 20px;
    line-height: 1.1;
    display: block;
    border: none;
    outline: none;
}
.wrap__icon-about-us,
.wrap__contacts-icon {
  cursor: pointer;
  bottom: 3px;
  right: 0px;
  display: none;
}
}
@media screen and (min-width: 1100px)
 {
 
.page-contacts__container {
  width: 100%;
  height: 100%;
  display: flex;
}
.page-contacts__address {
  display: flex;
flex-direction: column;
  gap: 50px;
  padding-top: 110px;
  position: relative;
}
.mail__link,
.phone__link,
.address__description {
  font-family: inherit;
  color: #fff;
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 50px;
}
.phone__link {
  margin-bottom: 0px;
}
.mail__link {
  margin-bottom: 0px;
}
.phone__list {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.page__item-contacts {
  display: none;
  position: absolute;
  right: 20px;
  top: 20px;
}
.address__map {
  width: 620px;
}
.contacts__date {
  display: flex;
  flex-direction: row;
  gap: 30%;
}
.wrap-contacts__link {
 width: 100%;
/*  transition: transform 7s ease;*/
  left: 0px;
 z-index: 52;
}

.hero-link__about-us{
 background-color: transparent;
  color: #fff;
  border: none;
  font-size: 18px;
  letter-spacing: 0.5;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
  transform: rotate(-90deg)  translate(30%, -70%);
  transform: translateX(-10%);
}

.hero-link__contacts {
  background-color: transparent;
  color: #fff;
  border: none;
  font-size: 18px;
  letter-spacing: 0.5;
    font-weight: 500;
    line-height: 1.2;
  text-transform: uppercase;
  transform: rotate(-90deg)  translate(30%, -100%);
  transform: translateX(-10%);
}

.button__contacts {
  justify-content: center;
  flex-direction: column;
  /*align-items: self-end;*/
  align-items: flex-end;
  gap: 5px;
  display: flex;
  position: relative;
}


/*.contacts-link__content {
  margin-top: 20px;
  margin-right: 20px;
  justify-content: center;
  flex-direction: column;
align-items: flex-end;
  gap: 5px;
  display: flex;
  position: relative;
}*/
.contacts-link__content-mob {
  justify-content: center;
  flex-direction: column;
align-items: flex-end;
  gap: 5px;
  display: flex;
  position: relative;
}

.wrap__contacts-icon::after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #fff;
  right: 0;
}

.contacts-toggle {
  text-align: right;
  text-transform: uppercase;
  cursor: pointer;
  color: #fff;
  background-color: #000;
  border: none;
  font-size: 24px;
  font-weight: 400;
  outline: none;

  transition: transform 1s ease;
}
 }